    .Page1 {
        width: 100vw;
        height: 100vh;
        background-image: url(./public/image/thomas-lefebvre-mT1uxKaegSQ-unsplash.jpg);
        background-position: center;
        background-size: cover;
        overflow: scroll;
    }
    
    .part1-Page1 {
        width: 95%;
        margin: 0 auto;
        /* background-color: aqua; */
    }
    
    .part2-Page1 {}
    
    .part2-Page1 div {
        /*background-color: #d7ecff5e;
        */
        background-color: #009b8c7c;
        width: 95%;
        display: flex;
        flex-direction: row-reverse;
        margin: 10px auto;
        padding: 5px;
        padding-bottom: 3px;
        border-radius: 16px;
        border: 2pt solid #009b8b;
        -webkit-border-radius: 16px;
        -moz-border-radius: 16px;
        -ms-border-radius: 16px;
        -o-border-radius: 16px;
    }
    
    .part2-Page1 div div {
        background-color: #d7ecff00;
        padding: 0px;
        margin: 0 0;
        width: auto;
        border-radius: 0px;
        /*background-color: aqua;*/
        -webkit-border-radius: 0px;
        -moz-border-radius: 0px;
        -ms-border-radius: 0px;
        -o-border-radius: 0px;
        border: none;
    }
    
    .part2-Page1 div .text-Page1 {
        /* background-color: aqua; */
        padding: 7px;
    }
    
    .part2-Page1 div .text-Page1 .title-Page1 p {
        font-size: medium;
        font-weight: 600;
    }
    
    .part2-Page1 div .icons-Page1 {
        padding: 5px;
    }
    
    .part2-Page1 div .icons-Page1 .icon-Page1 {
        width: 60px;
        height: 60px;
        /* background-color: #d7ecffa2; */
        border-radius: 50%;
        /* border: 2pt solid #009b8b; */
        border: 2pt solid #d7ecffa2;
        /* box-shadow: 1px 1px 1px 0.5px #d7ecff3a; */
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
    
    .part2-Page1 div .icons-Page1 #icon1-Page {
        background-image: url('./public/icons/empty-battery.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon2-Page {
        background-image: url('./public/icons/unlock.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon3-Page {
        background-image: url('./public/icons/telephone.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon4-Page {
        background-image: url('./public/icons/alert.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon5-Page {
        background-image: url('./public/icons/megaphone.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }
    
    .part2-Page1 div .icons-Page1 #icon6-Page {
        background-image: url('./public/icons/plug.png');
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
    }