.Setting {
    width: 100vw;
    height: 100vh;
    background-color: aliceblue;
    background-image: url('./public/image/thomas-lefebvre-mT1uxKaegSQ-unsplash.jpg');
    background-size: cover;
    background-position: center;
}

.header-Setting {
    width: 100%;
    height: 65px;
    background-color: #009b8c;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}

.header-Setting .backPart-Setting {
    width: 50px;
    height: 50px;
    /* background-color: red; */
    background-image: url('./public/icons/back-left-arrow-circular-button-outline.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 65%;
    transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -o-transform: rotate(-180deg);
}

.menu-Setting {
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    font-size: medium;
    font-weight: 600;
    /* text-shadow: 0.5px 0.5px black; */
}

.part-Setting {
    width: 100%;
    /* background-color: red; */
    display: flex;
    justify-content: space-between;
    /* padding: 10px; */
    margin-top: 20px;
}

.item-Setting {
    background-color: #009b8c7a;
    /*padding: 10px;
    */
    margin: 10px;
    border-radius: 15px;
    border: 1.5pt solid #009b8c;
    overflow: hidden;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    /* color: #d7ecffa2; */
}

.item-Setting .icon-Setting {
    width: 100px;
    height: 100px;
    /* background-color: blue; */
    /* margin-left: 5px; */
}

.item-Setting .text-Setting {
    width: 90px;
    /* background-color: aqua; */
    text-align: center;
}

.item-Setting .text-Setting p {
    width: 90px;
    /* background-color: blueviolet; */
}

