.OutPuts {
    width: 100vw;
    height: 100vh;
    background-color: #009b8c7c;
    font-size: medium;
    font-weight: 600;
}

#part1-OutPuts {
    width: 97%;
    /* height: 10px; */
    /* background-color: red; */
    margin: 10px auto;
}

#part1-OutPuts .item-OutPuts {
    width: 100%;
    height: 60px;
    background-color: #009b8c81;
    display: flex;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    overflow: hidden;
    margin-top: 10px;
    justify-content: center;
    border: 2pt solid #009b8b;
}

#part1-OutPuts .item-OutPuts .name-OutPuts,
#part1-OutPuts .item-OutPuts .state-OutPuts {
    width: 45%;
    height: 100%;
    /* background-color: aqua; */
    /* border: 2pt solid black; */
    /* flex-direction: row; */
    padding: 2px;
}

#part1-OutPuts .item-OutPuts .name-OutPuts {
    justify-content: right;
    text-align: right;
    display: flex;
}

#part1-OutPuts .item-OutPuts .state-OutPuts .inputPart-OutPuts {
    width: 65px;
    height: 30px;
    background-color: #cfd9e0;
    border-radius: 35px;
    -webkit-border-radius: 35px;
    -moz-border-radius: 35px;
    -ms-border-radius: 35px;
    -o-border-radius: 35px;
    margin-top: 15px;
    margin-left: 15px;
}

@keyframes state-off {
    0% {
        margin-left: 35px;
    }
    100% {
        margin-left: 5px;
    }
}

@keyframes state-on {
    0% {
        margin-left: 5px;
    }
    100% {
        margin-left: 35px;
    }
}

#part1-OutPuts .item-OutPuts .state-OutPuts .inputPart-OutPuts .input-OutPuts {
    position: relative;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: #ffffff;
    top: 2.5px;
}

#part1-OutPuts .item-OutPuts .name-OutPuts span p {
    /*height: 100%;
    */
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}