.Remotes {
    width: 100vw;
    height: 100vh;
    background-color: #009b8c7c;
    font-size: 11pt;
    /* font-weight: 600; */
}

#part2-Remotes {
    width: 95%;
    height: 90vh;
    border: 2pt solid #009b8c;
    margin: 10px auto;
    overflow: hidden;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
}

#part2-Remotes .items-Remotes {
    width: 97%;
    height: 75vh;
    /* background-color: pink; */
    margin: 0 auto;
    margin-top: 1px;
}

#part2-Remotes .items-Remotes .item-Remotes {
    position: relative;
    background-color: #009b8c65;
    width: 100%;
    height: 46px;
    display: flex;
    justify-content: space-between;
    border-radius: 8px;
    overflow: hidden;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    top: 8px;
    left: 50%;
    padding: 2px;
    transform: translate(-50%);
    border: 2pt solid #009b8c;
    -webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    margin-top: 10px;
}

#part2-Remotes .items-Remotes .item-Remotes .state-Remotes,
#part2-Remotes .items-Remotes .item-Remotes .number-Remotes,
#part2-Remotes .items-Remotes .item-Remotes .name-Remotes,
#part2-Remotes .items-Remotes .item-Remotes .type-Remotes {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* background-color: red; */
}

#part2-Remotes .items-Remotes .item-Remotes .state-Remotes {
    position: relative;
    background-color: #ffbb00;
    border: 2pt solid #ffbb0094;
    /* width: 20%; */
    height: 30px;
    /* width: 75px; */
    /* margin-left: 5%; */
    top: 50%;
    box-shadow: 2pt 1pt 1pt rgba(255, 255, 255, 0.404);
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    border-radius: 5px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

#part2-Remotes .items-Remotes .item-Remotes .state-Remoteso {
    /* background-color: rgb(250, 250, 162); */
    width: 23px;
    height: 23px;
    overflow: hidden;
    background-image: url('./public/icons/x.png');
    background-position: center;
    background-size: 70%;
    background-repeat: no-repeat;
    /* margin-top: 2px; */
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border: 2pt solid #d7ecffbe;
}

.table-Remotes .title-Remotes {
    width: 100%;
    height: 6vh;
    /* background-color: orange; */
    display: flex;
    justify-content: space-between;
    border-bottom: 2pt solid #009b8c;
    font-size: medium;
    font-weight: 600;
}

.table-Remotes .title-Remotes div {
    width: 30%;
    height: 100%;
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.table-Remotes .title-Remotes div p {
    /* background-color: aqua; */
}

.add-Remotes {
    width: 19vh;
    position: static;
    height: 19vh;
    /* background-color: blueviolet; */
    padding: 5px;
    /* bottom: 2.5%; */
    /* margin-bottom: 0px; */
    /* float: inline-end; */
    /* background-color: #009b8cc0; */
}

.add-Remotes .addIcon-Remotes {
    width: 47px;
    height: 47px;
    /* /*background-color: red; */
    margin: 0 auto;
    background-image: url('./public/icons/plus\(1\).png');
    background-position: center;
    background-size: 55%;
    background-repeat: no-repeat;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border: 3pt solid #d7ecffbe;
    float: left;
    background-color: #009b8c;
}